<template>
  <div class="login-register">
    <mu-tabs :value.sync="activeTab" full-width color="secondary" inverse>
      <mu-tab value="tab1">{{ $t('account.userLogin') }}</mu-tab>
      <mu-tab value="tab2">{{ $t('account.userRegister') }}</mu-tab>
    </mu-tabs>
    <form
      v-if="activeTab === 'tab1'"
      class="login-form"
      @submit.prevent="doLogin"
    >
      <mu-text-field
        type="email"
        name="email"
        :label="$t('account.email')"
        :fullWidth="true"
        :labelFloat="true"
        v-model="email"
        :required="true"
        lazy
      />
      <mu-text-field
        type="password"
        :label="$t('account.pwd')"
        name="password"
        v-model='password'
        :fullWidth="true"
        :labelFloat="true"
        :required="true"
        lazy
      />
      <mu-button color="primary" type='submit' fullWidth>
        {{ $t('account.login') }}
      </mu-button>
    </form>
    <form
      v-if="activeTab === 'tab2'"
      class="register-form"
      @submit.prevent="doRegister"
    >
      <mu-text-field
        :label="$t('account.nick')"
        name="nick"
        :fullWidth="true"
        :labelFloat="true"
        v-model='nick'
        :maxLength="10"
        :required="true"
      />
      <mu-text-field
        type="email"
        name="email"
        :label="$t('account.email')"
        v-model='email'
        :fullWidth="true"
        :labelFloat="true"
        :required="true"
      />
      <mu-text-field
        type="password"
        :label="$t('account.pwd')"
        v-model='password'
        name="password"
        :fullWidth="true"
        :labelFloat="true"
        :required="true"
      />
      <mu-button color="primary" type='submit' fullWidth>
        {{ $t('account.register') }}
      </mu-button>
    </form>
  </div>
</template>
<script>
import BaaS from 'minapp-sdk'

export default {
  name: 'LoginForm',
  props: {

  },
  data () {
    return {
      activeTab: 'tab1',
      email: '',
      password: '',
      nick: ''
    }
  },
  methods: {
    doLogin (e) {
      BaaS.auth.login({
        email: this.email,
        password: this.password
      }).then((user) => {
        this.$store.commit('SET_CURRENT_USER', user.toJSON())
        this.$emit('logined')
      }).catch(err => {
        console.error(err)
        this.$toast.error('登录失败')
      })
    },
    doRegister (e) {
      if (this.nick.length <= 10) {
        BaaS.auth.register({
          password: this.password,
          email: this.email
        }).then(user => {
          user.set('displayName', this.nick).update().then(res => {
            console.log(res)
            // this.$store.commit('SET_CURRENT_USER', user.toJSON())
            // this.$emit('logined')
          })
        }).catch(err => {
          console.error(err)
          this.$toast.error('注册失败')
        })
      }
    }
  }
}
</script>
<style lang="stylus" scoped src='./login.styl' />
