import {
  CHANGE_CANVAS_COLOR,
  CHANGE_CANVAS_LINE_WIDTH,
  CHANGE_CANVAS_LAYER,
  CHANGE_CANVAS_BG_COLOR,
  CHANGE_CANVAS_SHAPE_TYPE,
  CHANGE_CANVAS_OPACITY,
  CHANGE_CANVAS_VIEW_SIZE
} from '../mutation-types'
import { convertHex } from '../../libs/utils'

const state = {
  color: '#1d1d1d',
  colorIndex: 3,
  lineWidth: 2,
  bgColor: '#fff',
  layerType: 'C',
  shapeType: 'L',
  isOpacity: false,
  viewSize: [900, 506]
}

const getters = {
  currentBgColor: state => state.bgColor,
  isOpacity: state => state.isOpacity,
  currentColor: state => state.isOpacity ? convertHex(state.color, 50) : state.color,
  currentLineWidth: state => state.lineWidth,
  currentLayerType: state => state.layerType,
  colorIndex: state => state.colorIndex,
  currentShapeType: state => state.shapeType,
  viewSize: state => {
    let aw = window.screen.availWidth
    const ah = window.screen.availHeight
    if (Math.min(aw, ah) < 450) {
      if (aw > 560) {
        aw = 560
      }
      const rate = aw < ah ? 4 / 3 : 3 / 4
      const h = parseInt(rate * aw, 10)
      return [aw, h]
    }
    return state.viewSize
  }
}

const mutations = {
  [CHANGE_CANVAS_BG_COLOR] (state, color) {
    state.bgColor = color
  },
  [CHANGE_CANVAS_COLOR] (state, { color, idx }) {
    state.color = color
    state.colorIndex = idx + 1
  },
  [CHANGE_CANVAS_LINE_WIDTH] (state, width) {
    state.lineWidth = width
  },
  [CHANGE_CANVAS_LAYER] (state, layerType) {
    state.layerType = layerType
  },
  [CHANGE_CANVAS_SHAPE_TYPE] (state, shapeType) {
    state.shapeType = shapeType
  },
  [CHANGE_CANVAS_OPACITY] (state, isOp) {
    state.isOpacity = isOp
  },
  [CHANGE_CANVAS_VIEW_SIZE] (state, size) {
    state.viewSize = size
  }
}

export default {
  state,
  getters,
  mutations
}
