<template>
  <div
    ref='layerContainer'
    class="canvas-layer-wrap"
    :style='layerContainerHeight'>
    <canvas
      v-if='!isPlayer'
      v-show='false'
      ref="pdfLayer"
      :style="pdfLayerStyle"
      :width="pdfCanvasW"
      :height="pdfCanvasH"></canvas>
    <div class="canvas-layer" :style="layerStyle + bgColor">
      <canvas
        v-if='needCapture'
        v-show='false'
        ref='captureLayer'
        :style="captureLayerStyle"
        :width="canvasW"
        :height="captureH"
      ></canvas>
      <canvas
        v-show='false'
        ref="fakeLayer"
        :style="layerStyle"
        :width="canvasW"
        :height="canvasH"></canvas>
      <canvas
        ref="stageLayer"
        class="stage-layer"
        :style="layerStyle"
        :width="canvasW"
        :height="canvasH"></canvas>
      <canvas
        ref="displayLayer"
        class="display-layer"
        :style="layerStyle"
        :width="canvasW"
        :height="canvasH"></canvas>
      <canvas
        v-if='isPlayer'
        ref='syncLayer'
        class='sync-layer'
        :style="layerStyle"
        :width="canvasW"
        :height="canvasH"
        ></canvas>
      <canvas
        v-if='!isPlayer'
        ref="editLayer"
        class="edit-layer"
        @mousedown='handleMousedown'
        @touchstart='handleMousedown'
        @mousemove='handleMousemove'
        @touchmove='handleMousemove'
        @mouseup='handleMouseup'
        @touchend='handleMouseup'
        @mouseout='handleMouseout'
        :style="layerStyle + cursorStyle"
        :width="canvasW"
        :height="canvasH"></canvas>
      <div v-if='isPlayer' v-show='showCursor' class="sync-cursor" :style="syncCursorPos"></div>
    </div>
  </div>
</template>
<script>
import simplify from 'simplify-path'
import shortid from 'shortid'
import {
  clearRectByCurve,
  drawCurve,
  getRectByPos,
  checkCollision,
  renderEraserSelectedObjs,
  drawLine,
  drawRect,
  drawEllipse,
  drawArrowLine,
  // drawRough,
  clearCtxRect
} from '../../libs/canvas.js'
import { convertHex } from '../../libs/utils'
import { getNewCDNPath, getColor } from '../../libs/api'
import { CDN_PF } from '../../libs/constant'
import { EventBus } from '../../libs/event-bus.js'

export default {
  name: 'CanvasLayer',
  props: {
    wrapHeight: {
      default: 506,
      type: Number,
      require: true
    },
    isPlayer: {
      default: false,
      type: Boolean
    },
    width: {
      default: 900,
      type: Number
    },
    playerBgColor: {
      default: '#fff',
      type: String
    },
    needCapture: {
      default: false,
      type: Boolean
    }
  },

  data () {
    return {
      height: this.wrapHeight,
      curX: 0,
      curY: 0,
      curOp: 1,
      curColor: '#333333',
      scaleRate: 1,
      straX: false,
      straY: false,
      pdfHeight: this.wrapHeight,
      showCursor: false
    }
  },

  mounted () {
    this.imageDict = {}
    this.canvasObjs = []
    this.earseHistoryObjs = []
    this._points = []
    this.ctxScaleRatio()
    if (this.isPlayer) {
      this.setupSyncLayer()
    } else {
      this.setupEditLayer()
      document.addEventListener('keydown', this.handleKeydown)
      document.addEventListener('keyup', this.handleKeyup)
    }
  },

  beforeDestroy () {
    if (!this.isPlayer) {
      document.removeEventListener('keydown', this.handleKeydown)
      document.removeEventListener('keyup', this.handleKeyup)
    }
  },

  computed: {
    layerContainerHeight () {
      if (this.isPlayer) {
        return 'height:' + this.wrapHeight * this.scaleRate + 'px;'
      }
    },
    layerStyle () {
      return 'width:' + this.width * this.scaleRate +
      'px;height:' + this.height * this.scaleRate + 'px;'
    },
    pdfLayerStyle () {
      return 'width:' + this.width + 'px;height:' + this.pdfHeight + 'px;'
    },
    captureLayerStyle () {
      return 'width:' + this.width + 'px;height:' + this.wrapHeight + 'px;'
    },
    captureH () {
      return this.wrapHeight * window.devicePixelRatio
    },
    // pdf convert x2
    pdfCanvasH () {
      return this.pdfHeight * 2
    },
    pdfCanvasW () {
      return this.width * 2
    },
    bgColor () {
      if (this.isPlayer) {
        return 'background-color:' + this.playerBgColor
      } else {
        return 'background-color:' + this.$store.getters.currentBgColor
      }
    },
    canvasW () {
      return this.width * window.devicePixelRatio * this.scaleRate
    },
    canvasH () {
      return this.height * window.devicePixelRatio * this.scaleRate
    },
    color () {
      return this.$store.getters.currentColor
    },
    cursorStyle () {
      switch (this.layerType) {
        case 'E':
        case 'S':
          return 'cursor: crosshair;'
        default:
          const url = "url('" + CDN_PF + '/img/circle_' + this.$store.getters.colorIndex + ".svg')"
          if (window.devicePixelRatio > 1) {
            return 'cursor: -webkit-image-set(' + url + ' 2x) 6 6, auto;'
          } else {
            return 'cursor: ' + url + ' 6 6, auto;'
          }
      }
    },
    tolerance () {
      return this.$store.getters.preferences.tolerance
    },
    lineWidth () {
      return this.$store.getters.currentLineWidth
    },
    layerType () {
      return this.$store.getters.currentLayerType
    },
    shapeType () {
      return this.$store.getters.currentShapeType
    },
    syncCursorPos () {
      return 'transform: translateX(' +
      this.curX * this.scaleRate + 'px) translateY(' + this.curY * this.scaleRate +
      'px) scale(' + this.scaleRate + ',' + this.scaleRate + '); opacity:' +
      this.curOp + '; background-color:' + this.curColor + ';width: 12px;height: 12px;' +
      'border-radius: 12px;margin-top: -6px;margin-left: -6px;'
    }
  },
  watch: {
    layerType: function (newV, oldV) {
      this.setupEditLayer()
    },
    scaleRate: function (newV, oldV) {
      this.$nextTick(() => {
        this.ctxScaleRatio()
      })
    }
  },
  methods: {
    ctxScaleRatio () {
      [
        'displayLayer',
        'stageLayer',
        'editLayer',
        'fakeLayer',
        'syncLayer'
      ].forEach(layer => {
        const obj = this.$refs[layer]
        if (obj) {
          obj.getContext('2d').scale(window.devicePixelRatio, window.devicePixelRatio)
        }
      })
    },
    setupEditLayer () {
      const editCtx = this.getLayerCtx('editLayer')
      switch (this.layerType) {
        case 'C': // 常规笔
          break
        case 'E': // 橡皮擦
          editCtx.strokeStyle = '#ff8619'
          editCtx.fillStyle = 'rgba(255, 255, 255,0.4)'
          editCtx.lineCap = 'round'
          editCtx.lineJoin = 'round'
          editCtx.lineWidth = 1
          break
        case 'S':
          break
      }
    },
    setupSyncLayer () {

    },
    scrollLayerContainer (y) {
      this.$refs.layerContainer.scrollTop = y * this.scaleRate
    },
    preloadStage (bg, id) {
      return new Promise((resolve, reject) => {
        if (this.imageDict[id]) {
          resolve(this.imageDict[id])
        } else {
          let img = new window.Image()
          // img.crossOrigin = 'Anonymous'
          img.onload = () => {
            this.imageDict[id] = img
            img = null
            resolve(this.imageDict[id])
          }
          img.src = getNewCDNPath(bg.url)
        }
      })
    },
    async preloadStages (stages) {
      for (let obj of stages) {
        if (obj.bg) {
          await this.preloadStage(obj.bg, obj.id)
        }
      }
    },
    updateHeight (h) {
      if (h < this.wrapHeight) {
        h = this.wrapHeight
      }
      return new Promise((resolve, reject) => {
        if (h !== this.height) {
          this.height = h
          // 此处异步更新canvas dom，需等dom更新后再次scale canvas
          // 大概耗时100ms
          this.$nextTick(() => {
            this.ctxScaleRatio()
            resolve()
          })
        } else {
          this.clearCtx('stageLayer')
          resolve()
        }
      })
    },
    async setStageLayer (bg, id) {
      if (!bg.url) {
        return
      }
      EventBus.$emit('load:start')
      await this.preloadStage(bg, id).then((img) => {
        let width = bg.w
        let height = bg.h
        if (width > this.width) {
          height = (this.width * height) / width
          width = this.width
        }
        this.getLayerCtx('stageLayer').drawImage(
          img, 0, 0, width * this.scaleRate, height * this.scaleRate
        )
        EventBus.$emit('load:end')
      })
    },
    getPointBy (e) {
      if (e.touches) {
        const {
          pageX,
          pageY,
          target: {
            offsetParent: { offsetLeft, offsetTop }
          }
        } = e.changedTouches[0]
        return [
          parseInt(pageX - offsetLeft, 10),
          parseInt(pageY - offsetTop, 10)
        ]
      } else {
        return [
          parseInt(e.layerX, 10),
          parseInt(e.layerY, 10)
        ]
      }
    },
    handleMousedown (e) {
      const point = [this._px, this._py] = this.getPointBy(e)
      this._dragging = true
      switch (this.layerType) {
        case 'C':
          this._points.length = 0
          this._points.push(point)
          break
        case 'E':
          break
        case 'S':
          break
      }
    },
    handleMouseout () {
      if (this._dragging) {
        this._dragging = false
        this.handleMouseup()
      }
    },
    handleMousemove (e) {
      const point = this.getPointBy(e)
      const ctx = this.getLayerCtx('editLayer')
      const { x, y, w, h } = getRectByPos(point[0], point[1], this._px, this._py, this.scaleRate)
      switch (this.layerType) {
        case 'C':
          if (!this._dragging) {
            this.$emit('capture', {
              ac: 'CM',
              data: [point[0] / this.scaleRate, point[1] / this.scaleRate]
            })
            return
          }
          if (this.straX) {
            if (this.stra_y) {
              point[1] = this.stra_y
            } else {
              this.stra_y = point[1]
            }
          }
          if (this.straY) {
            if (this.stra_x) {
              point[0] = this.stra_x
            } else {
              this.stra_x = point[0]
            }
          }
          if (this._px === point[0] && this._py === point[1]) {
            return
          }
          this._px = point[0]
          this._py = point[1]
          this._points.push(point)
          this._points = simplify.radialDistance(this._points, this.tolerance)
          const data = {
            co: this.color,
            wi: this.lineWidth,
            ty: this.layerType,
            po: this._points.slice()
          }
          this.$emit('capture', { ac: 'CSync', data })
          this.renderCurve(data, 'editLayer', true)
          break
        case 'E':
          if (!this._dragging) {
            return
          }
          this.clearCtx('editLayer')
          ctx.fillRect(x, y, w, h)
          ctx.save()
          ctx.setLineDash([5])
          ctx.strokeRect(x, y, w, h)
          ctx.restore()
          // 碰撞检测
          const objs = checkCollision(this.canvasObjs, { x, y, w, h })
          renderEraserSelectedObjs(ctx, objs, this.scaleRate)
          break
        case 'S':
          if (!this._dragging) {
            return
          }
          if (this._prev_shape_rect) {
            clearCtxRect(ctx, this._prev_shape_rect, this.scaleRate)
          }
          this._prev_shape_rect = [x - 10, y - 10, w + 20, h + 20]
          const sobj = {
            ty: 'S',
            co: this.color,
            sty: this.shapeType,
            data: [
              this._px / this.scaleRate,
              this._py / this.scaleRate,
              point[0] / this.scaleRate,
              point[1] / this.scaleRate
            ]
          }
          this.renderShape(sobj, 'editLayer')
          break
      }
    },
    handleMouseup (e) {
      this._dragging = false
      this.stra_y = null
      this.stra_x = null
      const point = this.getPointBy(e)
      const rect = getRectByPos(point[0], point[1], this._px, this._py, this.scaleRate)
      switch (this.layerType) {
        case 'C':
          if (this._points.length > 0) {
            const data = {
              po: this._points.slice(),
              co: this.color,
              wi: this.lineWidth,
              ty: this.layerType
            }
            this.clearCtx('editLayer', this._points)
            this.$emit('capture', { ac: 'CEnd', data })
            this.renderCurve(data)
            this._points.length = 0
          }
          break
        case 'E':
          this.clearCtx('editLayer')
          // 传递框选后的坐标点，以供碰撞检测 TODO
          const objs = checkCollision(this.canvasObjs, rect)
          const ids = objs.map(p => p.id)
          if (!ids.length) {
            console.log('no earse')
            return
          }
          this.removeCanvasObjs(ids)
          // 将本次擦除笔迹记录到队列
          this.earseHistoryObjs.push(objs)
          this.$emit('capture', { ac: 'E', data: ids })
          // 重新渲染 displayLayer
          this.reRenderCanvasData()
          break
        case 'S':
          const sobj = {
            ty: 'S',
            co: this.color,
            sty: this.shapeType,
            data: [
              this._px / this.scaleRate,
              this._py / this.scaleRate,
              point[0] / this.scaleRate,
              point[1] / this.scaleRate
            ]
          }
          if (this._prev_shape_rect) {
            clearCtxRect(
              this.getLayerCtx('editLayer'),
              this._prev_shape_rect,
              this.scaleRate
            )
            this._prev_shape_rect = null
          }
          this.renderShape(sobj)
          this.$emit('capture', { ac: 'SEnd', data: sobj })
          break
      }
    },
    // 重绘上一次擦除的若干笔迹
    redrawEarseHistory () {
      const historyObjs = this.earseHistoryObjs.pop()
      if (historyObjs) {
        // 批量渲染擦除笔迹
        this.batchRenderObjs(historyObjs)
        // 重新将擦除笔迹加回笔迹队列
        for (let obj of historyObjs) {
          this.addCanvasObj(obj)
        }
        this.$emit('capture', { ac: 'UE', data: historyObjs })
      }
    },
    resetLayer () {
      this.canvasObjs = []
      this.clearCtx('displayLayer')
      this.clearCtx('editLayer')
      this.clearCtx('stageLayer')
      this.clearCtx('syncLayer')
    },
    reRenderCanvasData (objs) {
      if (objs) {
        this.canvasObjs = objs
      }
      if (this.canvasObjs.length) {
        this.batchRenderObjs(this.canvasObjs, true)
      } else {
        this.clearCtx('displayLayer')
      }
    },
    // curX: x, curY: y, curColor: c, curOp: 1
    cursorMove (obj) {
      if (this.showCursor) {
        for (let k of Object.keys(obj)) {
          this.$data[k] = obj[k]
        }
      }
    },
    removeCanvasObjs (objIds) {
      this.canvasObjs = this.canvasObjs.filter(obj => {
        return objIds.indexOf(obj.id) < 0
      })
      return this.canvasObjs
    },
    renderOneObj (obj, layerRef) {
      return new Promise((resolve, reject) => {
        switch (obj.ty) {
          case 'C':
            this.renderCurve(obj, layerRef)
            resolve()
            break
          case 'S':
            this.renderShape(obj, layerRef)
            resolve()
            break
        }
      })
    },
    async batchRenderObjs (objs, cover = false) {
      this.clearCtx('fakeLayer')
      for (let obj of objs) {
        await this.renderOneObj(obj, 'fakeLayer')
      }
      if (cover) {
        this.clearCtx('displayLayer')
      }
      this.getLayerCtx('displayLayer').drawImage(
        this.$refs.fakeLayer,
        0,
        0,
        this.width * this.scaleRate,
        this.height * this.scaleRate
      )
    },
    // layerRef [displayLayer, editLayer]
    renderCurve (obj, layerRef = 'displayLayer', isSync = false) {
      const ctx = this.getLayerCtx(layerRef)
      ctx.strokeStyle = ctx.fillStyle = getColor(obj.co)
      ctx.lineWidth = obj.wi * this.scaleRate
      ctx.lineCap = ctx.lineJoin = 'round'
      if (isSync) {
        let _prev = ctx._points
        if (_prev) {
          ctx.clearCurve = () => this.clearCtx(layerRef, _prev)
        } else {
          ctx.clearCurve = () => {}
        }
        ctx._points = obj.po
      } else if (layerRef === 'displayLayer') {
        this.addCanvasObj(obj)
      }
      drawCurve(ctx, obj.po, this.scaleRate, isSync)
    },
    renderShape (obj, layerRef = 'displayLayer') {
      const ctx = this.getLayerCtx(layerRef)
      switch (obj.sty) {
        case 'L':
          drawLine(ctx, obj.data, this.scaleRate, obj.co)
          break
        case 'AL':
          drawArrowLine(ctx, obj.data, this.scaleRate, obj.co)
          break
        case 'R':
          drawRect(ctx, obj.data, this.scaleRate, obj.co)
          // drawRough(ctx, obj.data, obj.co)
          break
        case 'C':
          drawEllipse(ctx, obj.data, this.scaleRate, obj.co)
          break
      }
      if (layerRef === 'displayLayer') {
        this.addCanvasObj(obj)
      }
    },

    getLayerCtx (layerRef) {
      return this.$refs[layerRef].getContext('2d')
    },

    addCanvasObj (obj) {
      obj.id = obj.id || shortid.generate()
      this.canvasObjs.push(obj)
    },
    // stageLayer, displayLayer, editLayer
    clearCtx (layerRef = 'editLayer', points) {
      if (!this.$refs[layerRef]) {
        return
      }
      const ctx = this.getLayerCtx(layerRef)
      if (points) {
        clearRectByCurve(ctx, points, this.scaleRate)
      } else {
        ctx.clearRect(0, 0, this.canvasW, this.canvasH)
      }
    },
    setScaleRateBy (opts = {}) {
      if (opts.origin) {
        this.scaleRate = 1
      } else {
        const targetWidth = opts.width || window.screen.width
        let rate = targetWidth / this.width
        if (opts.height) {
          if (this.wrapHeight * rate > opts.height) {
            rate = opts.height / this.wrapHeight
          }
        }
        this.scaleRate = Math.floor(rate * 1000) / 1000
      }
    },
    handleKeydown (e) {
      if (e.keyCode === 16) {
        this.straX = true
        this.$toast.success('横直线模式生效')
      } // shift key: 横线
      if (e.keyCode === 18) {
        this.straY = true
        this.$toast.success('竖直线模式生效')
      } // alt key: 竖线
    },
    handleKeyup (e) {
      if (e.keyCode === 16) {
        this.straX = false
        this.$toast.warning('横直线模式失效')
      }
      if (e.keyCode === 18) {
        this.straY = false
        this.$toast.warning('竖直线模式失效')
      }
    },
    toggleCursor (tof) {
      this.showCursor = tof
    },
    convertPDF2PNG (pdf, pageIdx) {
      return new Promise((resolve, reject) => {
        pdf.getPage(pageIdx).then((page) => {
          const initViewport = page.getViewport(1)
          const rate = (this.width * 2) / initViewport.width
          const viewport = page.getViewport(rate)
          const h = parseInt(viewport.height, 10)
          this.pdfHeight = h / 2
          this.$nextTick(() => {
            page.render({
              canvasContext: this.getLayerCtx('pdfLayer'),
              viewport
            }).then(() => {
              resolve(this.$refs.pdfLayer.toDataURL('image/webp', 0.8))
            })
          })
        })
      })
    },
    putImageAsBg (url) {
      return new Promise((resolve, reject) => {
        const img = new Image()
        const ctx = this.getLayerCtx('pdfLayer')
        img.onload = () => {
          let width = img.width
          let height = img.height
          if (width > this.width * 2) {
            height = (this.width * 2 * height) / width
            width = this.width * 2
          } else {
            // 图片宽度小于画板宽度，则无需压缩
            resolve(url)
            return
          }
          this.pdfHeight = height / 2
          this.$nextTick(() => {
            ctx.drawImage(img, 0, 0, width, height)
            resolve(this.$refs.pdfLayer.toDataURL('image/webp', 0.8))
          })
        }
        img.src = url
      })
    },
    drawPointer (ctx) {
      ctx.beginPath()
      const y = this.$refs.layerContainer.scrollTop * window.devicePixelRatio
      const ox = this.curX * window.devicePixelRatio
      const oy = this.curY * window.devicePixelRatio - y
      if (ox > 0 && oy > 0) {
        ctx.arc(
          ox,
          oy,
          12,
          0,
          Math.PI * 2,
          false
        )
        ctx.fillStyle = convertHex(this.curColor, this.curOp * 100)
        ctx.fill()
      }
    },
    captureCurrentImage () {
      const targetCtx = this.getLayerCtx('captureLayer')
      const lys = [
        'stageLayer',
        'displayLayer',
        'syncLayer'
      ]
      const y = this.$refs.layerContainer.scrollTop * window.devicePixelRatio
      lys.forEach(ly => {
        if (ly === 'stageLayer') {
          targetCtx.fillStyle = this.playerBgColor
          targetCtx.fillRect(
            0,
            0,
            this.canvasW,
            this.captureH
          )
        }
        targetCtx.drawImage(
          this.$refs[ly],
          0,
          y,
          this.canvasW,
          this.captureH,
          0,
          0,
          this.canvasW,
          this.captureH
        )
      })
      this.drawPointer(targetCtx)
      return this.$refs.captureLayer
    },
    genVideoCover (opts = {}) {
      const targetCtx = this.getLayerCtx('fakeLayer')
      targetCtx.fillStyle = '#fff'
      targetCtx.fillRect(0, 0, this.width, this.height)
      let hei = 180
      targetCtx.textBaseline = 'middle'
      targetCtx.font = '22px Roboto'
      targetCtx.fillStyle = '#666'
      const title = opts.title
      const titleW = targetCtx.measureText(title).width
      if (titleW < this.width) {
        const titleX = this.width / 2 - titleW / 2
        targetCtx.fillText(title, titleX, 80)
      }
      // render logo
      targetCtx.font = '16px Roboto'
      targetCtx.fillStyle = '#999'
      const logo = '由手绘微课录制'
      const logoX = this.width / 2 - targetCtx.measureText(logo).width / 2
      targetCtx.fillText(logo, logoX, this.height - 40)
      if (opts.author) {
        targetCtx.font = '18px Roboto'
        targetCtx.fillStyle = '#333'
        const textX = this.width / 2 - targetCtx.measureText(opts.author).width / 2
        targetCtx.fillText(opts.author, textX, hei)
      }
      opts.done(this.$refs.fakeLayer)
    }
  }
}
</script>
<style lang="stylus" src='./CanvasLayer.styl' scoped />
