import Vue from 'vue'
import shortid from 'shortid'
import moment from 'moment'
import BaaS from 'minapp-sdk'
import SparkMD5 from 'spark-md5'
import { dataURLtoBlob, Base64 } from './utils'
import {
  colorList, NEW_CDN, OLD_CDN, CDN_PF,
  UPYUN_BUCKET, UPYUN_SECRET,
} from './constant'

let upyunParams

export function getNewCDNPath (src) {
  if (src.match(OLD_CDN)) {
    return src.replace(OLD_CDN, NEW_CDN)
  }
  if (src.match(NEW_CDN)) {
    return src
  }
  return CDN_PF + src
}

const prepareUploadFormData = (file_type, opts = {}) => {
  const expiration = Math.round(new Date().getTime() / 1000) + 3600
  const thumb = opts.thumb || (file_type === 'image' && '/quality/75/format/webp/compress/true/progressive/true')
  let filename = moment().format('YYYY_MM_DD_HH_') + shortid.generate()
  if (thumb) {
    const hasCrop = thumb.match(/crop\/(\d+x\d+a+\d+a+\d+)/);
    if (hasCrop && hasCrop.length > 1)
      filename += hasCrop[1];
  }
  const path = '/file/' + file_type + '/' + filename;
  const options = {
    bucket: UPYUN_BUCKET,
    expiration,
    'save-key': path
  }
  if (thumb) {
    options['x-gmkerl-thumb'] = thumb
  }
  if (opts.apps){
    const save_as = path + '.' + opts.apps.f;
    options['apps'] = [
      {
        "name": "naga",
        "type": "audio",
        "save_as": save_as,
        "avopts": ('/f/' + opts.apps.f)
      }
    ]
  }
  const policy = Base64.encode(JSON.stringify(options))
  opts.done({
    signature: SparkMD5.hash(policy + '&' + UPYUN_SECRET),
    policy,
    bucket: UPYUN_BUCKET,
    'save-key': path,
    expiration
  })
}

// cache upyun params for image
export function requestUpyunParams (file_type, fd, done) {
  if (upyunParams) {
    if (fd) {
      Object.keys(upyunParams).forEach(k => {
        fd.append(k, upyunParams[k])
      })
      done(fd)
    } else {
      done(upyunParams)
    }
    return
  }
  prepareUploadFormData(file_type, {
    done: (obj) => {
      upyunParams = obj
      requestUpyunParams(file_type, fd, done)
    }
  })
}

function uploadCDN (formData, done) {
  Vue.axios({
    method: 'POST',
    url: 'https://v0.api.upyun.com/up17org',
    data: formData,
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  }).then((resp) => {
    const d = resp.data
    if (d.code === 200) {
      done(d)
    }
  })
}

export function uploadImg (file, isB64 = false) {
  return new Promise((resolve, reject) => {
    requestUpyunParams('image', (new FormData()), (formData) => {
      formData.append('file', isB64 ? dataURLtoBlob(file) : file)
      uploadCDN(formData, (d) => {
        resolve({
          url: d.url,
          h: (d['image-height'] / 2),
          w: (d['image-width'] / 2),
          size: d.file_size
        })
      })
    })
  })
}

export function uploadAudio (blob) {
  return new Promise((resolve, reject) => {
    prepareUploadFormData('audio', {
      apps: {
        f: 'MP3'
      },
      done: (upyunParams) => {
        const formData = new FormData()
        formData.append('file', blob)
        formData.append('content-type', 'audio/ogg')
        Object.keys(upyunParams).forEach(k => {
          formData.append(k, upyunParams[k])
        })
        uploadCDN(formData, (d) => {
          resolve(d.url)
        })
      }
    })
  })
}

export function fetchBlobAudioBy (url, done, fail) {
  Vue.axios({
    method: 'GET',
    url,
    responseType: 'arraybuffer'
  }).then((resp) => {
    const blob = new Blob([resp.data], {
      type: 'audio/ogg'
    })
    done(blob)
  }).catch(fail)
}

export function getVideos (opts = {}, done) {
  let Video = new BaaS.TableObject('video')
  let query = new BaaS.Query()
  let limit = 20
  let selectFields = ['-cd']
  if (opts.uid) {
    query.compare('created_by', '=', +opts.uid)
  } else if (opts.cateId) {
    query.compare('cate_id', '=', opts.cateId)
  }
  if (opts.uid || opts.cateId) {
    selectFields.push('-uinfo')
  }
  if (opts.recent || opts.hidePrivate) {
    query.compare('is_private', '!=', true)
  }
  if (opts.classid) {
    query.arrayContains('classids', [opts.classid])
  }
  if (opts.keyword) {
    query.matches('t', new RegExp(opts.keyword, 'i'))
  }
  if (opts.onlyCount) {
    Video.setQuery(query).select('created_at').find().then((res) => {
      if (res.statusCode === 200) {
        const count = res.data.meta.total_count
        console.log("共有 " + count + " 条记录");
        done(count);
      }
    })
    return
  }
  Video.setQuery(query).select(selectFields)
    .orderBy('-created_at')
    .limit(limit)
    .expand('created_by')
    .offset(opts.offset || 0)
    .find().then((res) => {
      if (res.statusCode === 200) {
        const results = res.data.objects
        console.log("共查询到 " + results.length + " 条记录", results);
        let list = []
        for (let obj of results) {
          list.push(Object.assign({}, obj, {
            duration: moment(obj.dur).format('mm:ss'),
            cover_url: getNewCDNPath(obj.cover_url),
            count: (obj.n || 0),
            c_at: moment(obj.created_at * 1000).format('lll')
          }))
        }
        done(list, res.data.meta.next, res.data.meta.total_count)
      }
    })
}

export function getVideo (id, done, fail) {
  let Video = new BaaS.TableObject('video')
  Video.expand('created_by').get(id).then((res) => {
    const cd = JSON.parse(res.data.cd)
    let audioObj = cd.canvas.find(r => r.ac === 'AU')
    done(Object.assign({}, res.data, {
      duration: moment(res.data.dur).format('mm:ss'),
      cover_url: getNewCDNPath(res.data.cover_url),
      count: (res.data.n || 0),
      c_at: moment(res.data.created_at * 1000).format('lll'),
      cd,
      ap: audioObj ? audioObj.data : res.data.ap
    }))
  }, fail)
}

export function removeVideo (id, done) {
  let Video = new BaaS.TableObject('video')
  Video.delete(id).then(done)
}

export function plusVideoCount (id) {
  let Video = new BaaS.TableObject('video')
  let vd = Video.getWithoutData(id)
  vd.incrementBy('n', 1)
  vd.update()
}

export function addVideo (json, done) {
  let Video = new BaaS.TableObject('video')
  let vd = Video.create()
  vd.set(json).save().then(res => {
    if (res.statusCode === 201) {
      console.log("创建video成功, objectId:" + res.data.id);
      done(res.data.id)
    }
  })
}

export function getColor(idx) {
  if (typeof idx === 'string' && idx.indexOf('#') === 0) {
    return idx
  }
  if (idx.indexOf('rgba') > -1) {
    return idx
  }
  return colorList[idx].value
}