import rough from 'roughjs'

export function drawRough (ctx, data, color) {
  const rc = rough.canvas(ctx.canvas)
  const rect = getRectByPos(...data)
  rc.rectangle(rect.x, rect.y, rect.w, rect.h, { stroke: color })
}

export function maxArray (arr) {
  return Math.max.apply(Math, arr)
}

export function minArray (arr) {
  return Math.min.apply(Math, arr)
}

export function getBoundingRect (points) {
  const xs = points.map(p => p[0])
  const ys = points.map(p => p[1])
  return ([
    minArray(xs),
    minArray(ys),
    maxArray(xs) - minArray(xs),
    maxArray(ys) - minArray(ys)
  ])
}

export function rounded (n, scaleRate) {
  if (scaleRate !== 1) {
    return (n * scaleRate) + 0.5 << 0
  } else {
    return n
  }
}

export function clearCtxRect (ctx, rect, scaleRate = 1) {
  const x = Math.round(rect[0] * scaleRate - ctx.lineWidth / 2 - 0.5)
  const y = Math.round(rect[1] * scaleRate - ctx.lineWidth / 2 - 0.5)
  const w = Math.round(rect[2] * scaleRate + ctx.lineWidth + 0.5)
  const h = Math.round(rect[3] * scaleRate + ctx.lineWidth + 0.5)
  ctx.clearRect(x, y, w, h)
}

export function clearRectByCurve (ctx, points, scaleRate = 1) {
  clearCtxRect(ctx, getBoundingRect(points), scaleRate)
  points = []
}

export function drawCurve (ctx, _points, scaleRate, clear = true) {
  if (clear) {
    ctx.clearCurve()
  }
  if (_points.length === 1) {
    const b = _points[0]
    ctx.beginPath()
    ctx.arc(rounded(b[0], scaleRate), rounded(b[1], scaleRate), ctx.lineWidth / 2, 0, Math.PI * 2, !0)
    ctx.closePath()
    ctx.fill()
    return
  }
  ctx.beginPath()
  const ox = rounded(_points[0][0], scaleRate)
  const oy = rounded(_points[0][1], scaleRate)
  ctx.moveTo(ox, oy)
  if (_points.length === 2) {
    ctx.lineTo(rounded(_points[1][0], scaleRate), rounded(_points[1][1], scaleRate))
    ctx.stroke()
    return
  }
  let i = 1
  while (i < _points.length - 2) {
    const c = rounded((_points[i][0] + _points[i + 1][0]) / 2, scaleRate)
    const d = rounded((_points[i][1] + _points[i + 1][1]) / 2, scaleRate)
    const x = rounded(_points[i][0], scaleRate)
    const y = rounded(_points[i][1], scaleRate)
    ctx.quadraticCurveTo(x, y, c, d)
    i++
  }
  const t1 = rounded(_points[i][0], scaleRate)
  const t2 = rounded(_points[i][1], scaleRate)
  const t3 = rounded(_points[i + 1][0], scaleRate)
  const t4 = rounded(_points[i + 1][1], scaleRate)
  ctx.quadraticCurveTo(t1, t2, t3, t4)
  ctx.stroke()
}

export function getRectByPos (tx, ty, stx, sty, scaleRate = 1) {
  return {
    x: Math.min(tx, stx) / scaleRate,
    y: Math.min(ty, sty) / scaleRate,
    w: Math.abs(stx - tx) / scaleRate,
    h: Math.abs(sty - ty) / scaleRate
  }
}

export function strokeArc (ctx, x, y, scaleRate = 1) {
  ctx.beginPath()
  ctx.arc(x * scaleRate, y * scaleRate, 4, 2 * Math.PI, false)
  ctx.closePath()
  ctx.lineWidth = 1
  ctx.stroke()
}

export function renderEraserSelectedObjs (ctx, objs, scaleRate = 1) {
  objs.forEach(path => {
    switch (path.ty) {
      case 'C':
        const startPoint = path.po[0]
        const endPoint = path.po[path.po.length - 1]
        strokeArc(ctx, startPoint[0], startPoint[1], scaleRate)
        strokeArc(ctx, endPoint[0], endPoint[1], scaleRate)
        break
      case 'MC':
        ctx.save()
        ctx.setLineDash([5])
        drawCurve(ctx, path.po, scaleRate, false)
        ctx.restore()
        break
      case 'S':
        switch (path.sty) {
          case 'L':
          case 'AL':
            strokeArc(ctx, path.data[0], path.data[1], scaleRate)
            strokeArc(ctx, path.data[2], path.data[3], scaleRate)
            break
          case 'C':
            drawEllipse(ctx, path.data, scaleRate)
            break
          case 'R':
            drawRect(ctx, path.data, scaleRate)
            break
        }
    }
  })
}

export function rectXRect (rect1, rect2) {
  let X1, X2, Y1, Y2, lineTest, res
  if (rect1 && rect2) {
    lineTest = (a, b) => {
      return !(a[1] < b[0] || a[0] > b[1])
    }
    X1 = [rect1.x, rect1.x + rect1.w]
    if (rect1.w < 0) {
      X1 = [X1[1], X1[0]]
    }
    X2 = [rect2.x, rect2.x + rect2.w]
    Y1 = [rect1.y, rect1.y + rect1.h]
    if (rect1.h < 0) {
      Y1 = [Y1[1], Y1[0]]
    }
    Y2 = [rect2.y, rect2.y + rect2.h]
    res = lineTest(X1, X2) && lineTest(Y1, Y2)
    return res
  } else {
    return false
  }
}

// opts = [unclude_curve, only_curve]
export function checkCollision (datasource, rect, opts = {}) {
  const selectedObjs = []
  const maxX = rect.x + rect.w
  const maxY = rect.y + rect.h
  datasource.forEach(path => {
    switch (path.ty) {
      case 'C':
      case 'MC':
        if (opts.unclude_curve) {
          break
        }
        for (let p of path.po) {
          if (p[0] >= rect.x && p[0] <= maxX && p[1] >= rect.y && p[1] <= maxY) {
            selectedObjs.push(path)
            break
          }
        }
        break
      case 'S':
        if (opts.only_curve) {
          break
        }
        const rectS = getRectByPos(...path.data)
        switch (path.sty) {
          case 'R':
          case 'C':
            if (rectXRect(rect, rectS)) {
              selectedObjs.push(path)
            }
            break
          case 'L':
          case 'AL':
            const sp = [path.data[0], path.data[1]]
            const ep = [path.data[2], path.data[3]]
            const wid = rectS.w
            const hei = rectS.h
            const leftTop = [
              rectS.x,
              rectS.y
            ]
            const rightBot = [
              rectS.x + rectS.w,
              rectS.y + rectS.h
            ]
            const per = wid / 7
            let i = 1
            const listP = [sp, ep]
            while (leftTop[0] + per * i < rightBot[0]) {
              const x = leftTop[0] + per * i
              const del = (hei / wid) * per * i
              let y
              if ((ep[1] - sp[1]) * (ep[0] - sp[0]) > 0) {
                y = del + leftTop[1]
              } else {
                y = rightBot[1] - del
              }
              listP.push([x, y])
              i += 1
            }
            // if (opts.ctx) {
            //   for (let p of listP) {
            //     opts.ctx.beginPath()
            //     opts.ctx.arc(p[0], p[1], 4, 2 * Math.PI, false)
            //     opts.ctx.lineWidth = 1
            //     opts.ctx.stroke()
            //   }
            // }
            for (let p of listP) {
              if (p[0] >= rect.x && p[0] <= maxX && p[1] >= rect.y && p[1] <= maxY) {
                selectedObjs.push(path)
                break
              }
            }
            break
        }
    }
  })
  return selectedObjs
}

export function isCircle (points) {
  const rect = this.getBoundingRect(points)
  const w = rect[2]
  const h = rect[3]
  if (Math.abs(w - h) < (w + h) / 20) {
    const mid = [rect[0] + w / 2, rect[1] + h / 2]
    const half = (w + h) / 4
    let isCircle = true
    for (let p of points) {
      const xd = Math.abs(p[0] - mid[0])
      const yd = Math.abs(p[1] - mid[1])
      const v = Math.pow((xd * xd + yd * yd), 0.5)
      if (Math.abs(half - v) > half / 5) {
        isCircle = false
        break
      }
    }
    return (isCircle && [mid, half])
  }
  return false
}

export function drawLine (ctx, spep, scaleRate = 1, color) {
  ctx.save()
  ctx.lineWidth = 2
  ctx.strokeStyle = color
  ctx.lineCap = 'round'
  ctx.lineJoin = 'round'
  ctx.beginPath()
  ctx.moveTo(spep[0] * scaleRate, spep[1] * scaleRate)
  ctx.lineTo(spep[2] * scaleRate, spep[3] * scaleRate)
  ctx.stroke()
  ctx.restore()
}

export function drawArrowLine (ctx, data, scaleRate = 1, color) {
  const headlen = 10
  ctx.save()
  ctx.lineWidth = 2
  if (color) {
    ctx.strokeStyle = color
  }
  ctx.lineCap = 'round'
  ctx.lineJoin = 'round'
  const tox = data[2]
  const toy = data[3]
  const angle = Math.atan2(
    data[3] - data[1],
    data[2] - data[0]
  )
  ctx.beginPath()
  ctx.moveTo(data[0] * scaleRate, data[1] * scaleRate)
  ctx.lineTo(data[2] * scaleRate, data[3] * scaleRate)
  ctx.moveTo(tox, toy)
  ctx.lineTo(tox - headlen * Math.cos(angle - Math.PI / 6), toy - headlen * Math.sin(angle - Math.PI / 6))
  ctx.moveTo(tox, toy)
  ctx.lineTo(tox - headlen * Math.cos(angle + Math.PI / 6), toy - headlen * Math.sin(angle + Math.PI / 6))
  ctx.stroke()
  ctx.restore()
}

export function drawRect (ctx, data, scaleRate = 1, color) {
  ctx.save()
  ctx.lineWidth = 2
  if (color) {
    ctx.strokeStyle = color
  }
  const rect = getRectByPos(...data, scaleRate)
  ctx.strokeRect(rect.x, rect.y, rect.w, rect.h)
  ctx.restore()
}

export function drawEllipse (ctx, data, scaleRate = 1, color) {
  const rect = getRectByPos(...data, scaleRate)
  const ox = rect.x + rect.w / 2
  const oy = rect.y + rect.h / 2
  ctx.save()
  ctx.lineWidth = 2
  if (color) {
    ctx.strokeStyle = color
  }
  ctx.beginPath()
  ctx.ellipse(ox, oy, rect.w / 2, rect.h / 2, 0, Math.PI * 2, false)
  ctx.stroke()
  ctx.restore()
}
