import shortid from 'shortid'
import {
  CHANGE_CURRENT_FRAME,
  INSERT_FRAME_BY_IMAGE,
  INSERT_FRAME_BY_IMAGES,
  REMOVE_FRAME_BY_NUM
} from '../mutation-types'
import { CDN_PF } from '../../libs/constant'

const state = {
  frameNo: 1,
  frameList: [{
    id: shortid.generate(),
    bg: {
      url: `${CDN_PF}/img/xcx/banner2.png`,
      w: 2800,
      h: 800
    }
  }]
}

const getters = {
  currentFrame: state => state.frameNo,
  frameList: state => state.frameList,
  currentFid: state => state.frameList[state.frameNo - 1].id
}

const mutations = {
  [CHANGE_CURRENT_FRAME] (state, frameNo) {
    if (frameNo > 0) {
      state.frameNo = frameNo
    }
    if (frameNo > state.frameList.length) {
      state.frameList.push({
        id: shortid.generate()
      })
    }
  },
  [INSERT_FRAME_BY_IMAGE] (state, img) {
    state.frameList.splice(state.frameNo, 0, {
      id: shortid.generate(),
      bg: img
    })
    state.frameNo += 1
  },
  [INSERT_FRAME_BY_IMAGES] (state, imgs) {
    const curLastFrameNo = state.frameList.length
    imgs.forEach(img => {
      state.frameList.push({
        id: shortid.generate(),
        bg: img
        // bg: {
        //   url: '',
        //   size: 0,
        //   w: 0,
        //   h: 0
        // }
      })
    })
    if (imgs.length > 0) {
      state.frameNo = curLastFrameNo + 1
    }
  },
  [REMOVE_FRAME_BY_NUM] (state, num) {
    state.frameList.splice(num - 1, 1)
  }
}

export default {
  state,
  getters,
  mutations
}
