<template>
  <div id="controlBar">
    <mu-popover
      :open.sync="openShapeCtrl"
      :trigger="shapeCtrlTrigger"
      placement="bottom">
      <mu-bottom-nav :value="shapeType" @change="handleChangeShapeType">
        <mu-bottom-nav-item value="L" :title="$t('ctrlBar.line')" icon="short_text" />
        <mu-bottom-nav-item value="AL" :title="$t('ctrlBar.arrowLine')" icon="trending_flat" />
        <mu-bottom-nav-item value="C" :title="$t('ctrlBar.circle')" icon="panorama_fish_eye" />
        <mu-bottom-nav-item value="R" :title="$t('ctrlBar.rect')" icon="crop_landscape" />
      </mu-bottom-nav>
    </mu-popover>
    <div class="main">
      <div class='layer-ctrl'>
        <mu-bottom-nav :value="bottomNav" @change="handleChange">
          <mu-bottom-nav-item
            value="C"
            :title="$t('ctrlBar.pen')"
            icon="edit" />
          <mu-bottom-nav-item
            ref="shapeBtn"
            value="S"
            :title="$t('ctrlBar.shape')"
            @click.native="toggleShapeCtrl"
            :icon='currentShapeIcon' />
          <mu-bottom-nav-item
            value="E"
            :title="$t('ctrlBar.eraser')"
            icon="crop_free" />
        </mu-bottom-nav>
      </div>
      <div class="func-btns">
        <mu-tooltip :content="$t('ctrlBar.undoErase')" placement='top'>
          <mu-button
            icon
            class="func-btn pc-only"
            @click="() => $emit('undo')">
            <mu-icon value="undo" />
          </mu-button>
        </mu-tooltip>
        <mu-tooltip :content="$t('ctrlBar.penWidth')" placement='top'>
          <mu-button
            icon
            class="func-btn pc-only"
            @click='linePopup = true'>
            <mu-icon value="line_weight" />
          </mu-button>
        </mu-tooltip>
        <mu-tooltip :content="$t('ctrlBar.penColor')" placement='top'>
          <mu-button
            icon
            class="func-btn pc-only"
            :style="colorPickerStyle"
            @click='colorPopup = true'>
            <mu-icon value="color_lens" />
          </mu-button>
        </mu-tooltip>
        <uploader
          class="func-btn"
          :autoStart='false'
          :options="uploadOptions"
          @file-added="onFileAdded">
          <uploader-btn
            :single='true'
            :attrs="uploadAttrs">
            <mu-tooltip :content="$t('ctrlBar.uploadPdf')" placement='top'>
              <mu-button icon>
                <mu-icon value="picture_as_pdf" />
              </mu-button>
            </mu-tooltip>
          </uploader-btn>
        </uploader>
        <mu-menu class="func-btn only-mobile" :open.sync="openMoreMenu" placement='top-end'>
          <mu-button icon>
            <mu-icon value="more_vert" />
          </mu-button>
          <mu-list slot="content">
            <mu-list-item button @click="() => $emit('undo')">
              <mu-list-item-action>
                <mu-icon value="undo" />
              </mu-list-item-action>
              <mu-list-item-title>{{$t('ctrlBar.undoErase')}}</mu-list-item-title>
            </mu-list-item>
            <mu-list-item button @click='linePopup = true'>
              <mu-list-item-action>
                <mu-icon value="line_weight" />
              </mu-list-item-action>
              <mu-list-item-title>{{$t('ctrlBar.penWidth')}}</mu-list-item-title>
            </mu-list-item>
            <mu-list-item button @click='colorPopup = true'>
              <mu-list-item-action>
                <mu-icon value="color_lens" />
              </mu-list-item-action>
              <mu-list-item-title>{{$t('ctrlBar.penColor')}}</mu-list-item-title>
            </mu-list-item>
          </mu-list>
        </mu-menu>
      </div>
    </div>
    <mu-dialog
      dialog-class="color-picker"
      :title="$t('ctrlBar.penColor')"
      :open.sync="colorPopup">
      <mu-flex wrap='wrap' justify-content='center'>
        <mu-flex
          v-for="(color,index) in colorList"
          :key='index'
          class="color-item"
          @click='handleColorSelect(color, index)'
          :style="'background:' + color">
        </mu-flex>
      </mu-flex>
      <mu-list>
        <mu-list-item button :ripple="false" @click="isOpacity = !isOpacity">
          <mu-list-item-title>
            {{$t('ctrlBar.opacity')}}
          </mu-list-item-title>
          <mu-list-item-action>
            <mu-switch v-model="isOpacity" readonly/>
          </mu-list-item-action>
        </mu-list-item>
      </mu-list>
    </mu-dialog>
    <mu-dialog
      dialog-class="line-slider-pop"
      :title="$t('ctrlBar.penWidth')"
      :open.sync="linePopup">
      <div class="title">
        {{$t('ctrlBar.penWidthVal', {px: lineWidth})}}
      </div>
      <div class="line-wrap">
        <div class="sample-line" :style="lineWidthStyle" />
      </div>
      <mu-slider
        v-model="lineWidth"
        :max="20"
        :step="1"
        :min="1"
      />
    </mu-dialog>
  </div>
</template>
<script>
import PDFJS from 'pdfjs-dist'
import { CDN_PF } from '../../libs/constant'
import { EventBus } from '../../libs/event-bus.js'

export default {
  name: 'ControlBar',
  props: {

  },
  data () {
    return {
      colorPopup: false,
      linePopup: false,
      openMoreMenu: false,
      colorList: [
        '#e74000',
        '#0088ff',
        '#1d1d1d',
        '#FFFF00',
        '#FF9E9D',
        '#5cb85c',
        '#5E405F',
        '#f5f5f5'
      ],
      uploadOptions: {
        testChunks: false,
        singleFile: true
      },
      uploadAttrs: {
        accept: 'application/pdf,image/*'
      },
      limitPageNum: 100,
      shapeCtrlTrigger: null,
      openShapeCtrl: false
    }
  },
  mounted () {
    PDFJS.workerSrc = CDN_PF + '/js/pdf.worker.js'
    this.shapeCtrlTrigger = this.$refs.shapeBtn.$el
  },
  computed: {
    bottomNav () {
      return this.$store.getters.currentLayerType
    },
    lineWidthStyle () {
      return 'height:' + this.lineWidth + 'px;background-color:' + this.color + ';'
    },
    colorPickerStyle () {
      return 'color:' + this.color
    },
    color () {
      return this.$store.getters.currentColor
    },
    lineWidth: {
      get () {
        return this.$store.getters.currentLineWidth
      },
      set (v) {
        this.$store.commit('CHANGE_CANVAS_LINE_WIDTH', v)
      }
    },
    isOpacity: {
      get () {
        return this.$store.getters.isOpacity
      },
      set (v) {
        this.$store.commit('CHANGE_CANVAS_OPACITY', v)
      }
    },
    layerType () {
      return this.$store.getters.currentLayerType
    },
    shapeType () {
      return this.$store.getters.currentShapeType
    },
    currentShapeIcon () {
      switch (this.$store.getters.currentShapeType) {
        case 'L':
          return 'short_text'
        case 'AL':
          return 'trending_flat'
        case 'C':
          return 'panorama_fish_eye'
        case 'R':
          return 'crop_landscape'
      }
    }
  },
  methods: {
    handleChange (val) {
      this.$store.commit('CHANGE_CANVAS_LAYER', val)
    },
    handleChangeShapeType (val) {
      this.$store.commit('CHANGE_CANVAS_SHAPE_TYPE', val)
      this.openShapeCtrl = false
    },
    toggleShapeCtrl () {
      this.openShapeCtrl = !this.openShapeCtrl
    },
    handleColorSelect (color, idx) {
      this.colorPopup = false
      this.$store.commit('CHANGE_CANVAS_COLOR', { color, idx })
    },
    onFileAdded (file) {
      const isImg = file.fileType.match(/image/)
      EventBus.$emit('load:start', isImg ? '正在插入背景图片' : '正在解析文件')
      const fr = new FileReader()
      if (isImg) {
        // 单张图片插入: 插入到当前页的下一页
        // 图片压缩处理
        fr.readAsDataURL(file.file)
        fr.onload = (ev) => {
          this.$emit('compressImage', ev.target.result)
        }
      } else {
        fr.readAsArrayBuffer(file.file)
        fr.onload = (ev) => {
          PDFJS.getDocument({
            data: ev.target.result,
            cMapUrl: '/js/cmaps/',
            cMapPacked: true
          }).then((pdf) => {
            EventBus.$emit('load:end')
            const missionNum = pdf.numPages
            if (missionNum > this.limitPageNum) {
              EventBus.$emit(
                'dialog',
                {
                  title: '提示',
                  msg: ('已超过文档上传页数限制: ' + this.limitPageNum + '/' + missionNum +
                  ', 继续操作，超出部分将不会被导入'),
                  confirm: () => {
                    this.$emit('convertPdf', { pdf, missionNum: this.limitPageNum })
                  }
                }
              )
            } else {
              this.$emit('convertPdf', { pdf, missionNum })
            }
          })
        }
      }
    }
  }
}
</script>
<style lang="stylus" src='./ControlBar.styl' scoped />
