<template>
  <div id="app" class="content">
    <mu-linear-progress :size="2" color='#EE0B5B' v-if="showLoading" />
    <router-view />
    <mu-dialog dialog-class="login-dialog" :open.sync="requireLogin">
      <mu-alert color="warning">
        <mu-icon value="priority_high"></mu-icon>
        {{ $t('app.requireLoginTip') }}
      </mu-alert>
      <login-form @logined='requireLogin = false' />
    </mu-dialog>
    <mu-dialog
      dialog-class="loading-dialog"
      :open.sync="showLoadingDialog">
      <mu-circular-progress color="secondary" :size="36"></mu-circular-progress>
      <div class="msg">{{loadingMsg || $t('app.loading')}}</div>
    </mu-dialog>
  </div>
</template>

<script>
import { EventBus } from './libs/event-bus.js'
import LoginForm from './components/account/login'

export default {
  name: 'App',
  components: {
    LoginForm
  },
  data () {
    return {
      requireLogin: false,
      showLoading: false,
      showLoadingDialog: false,
      loadingMsg: null
    }
  },
  created () {
    EventBus.$on('requireLogin', () => {
      this.requireLogin = true
    })
    EventBus.$on('load:start', (msg) => {
      if (msg) {
        this.loadingMsg = msg
        this.showLoadingDialog = true
        return
      }
      this.showLoading = true
    })
    EventBus.$on('load:end', () => {
      this.showLoadingDialog = false
      this.showLoading = false
    })
  }
}
</script>

