import {
  SET_CURRENT_USER,
  SET_PREFERENCE
} from '../mutation-types'

const state = {
  // nick, email
  currentUser: null,
  preferences: {
    tolerance: 4,
    lang: (navigator.language.match(/zh/) ? 'zh' : 'en')
  }
}

const getters = {
  currentUser: state => state.currentUser,
  preferences: state => state.preferences
}

const mutations = {
  [SET_CURRENT_USER] (state, user) {
    state.currentUser = user ? Object.assign({}, user, { uid: user.objectId }) : null
  },
  [SET_PREFERENCE] (state, objs) {
    state.preferences = Object.assign({}, state.preferences, objs)
  }
}

export default {
  state,
  getters,
  mutations
}
