<template>
  <section>
    <mu-appbar color="primary" :title="$t('preview.title')" :z-depth='0'>
      <mu-button icon slot="left" to="/">
        <mu-icon value="arrow_back" />
      </mu-button>
      <mu-button
        flat
        class='btn'
        primary
        slot="right"
        v-if="audioTrack.blob.size"
        @click='openUploadDialog'
      >
        <mu-icon left value='cloud_upload' />
        {{$t('preview.upload')}}
      </mu-button>
    </mu-appbar>
    <div class="preview-container">
      <player
        :audioUrl='audioTrack.url'
        :canvasObjs='canvasObjs'
        :stages='stages'
        :autoPlay='true'
        :bgColor='stageBgColor'
        :size="playerSize"
        :duration='duration' />
    </div>
    <mu-dialog
      :open.sync="dialog"
      width='460'
      :title="$t('preview.upload')">
      <mu-text-field
        ref="titleInput"
        fullWidth
        v-model.trim='title'
        required
        name="title"
        type='text'
        :maxLength='30'
        autofocus
        label="给本次录制的微课命名"
      />
      <mu-button
        flat
        slot="actions"
        @click="dialog = false"
        color='secondary'>
        {{$t('app.cancelBtn')}}
      </mu-button>
      <mu-button
        flat
        slot="actions"
        color='primary'
        @click="publishVideo">
        {{$t('preview.pubBtn')}}
      </mu-button>
    </mu-dialog>
  </section>
</template>
<script>
import Player from '@/components/base/player'
import { EventBus } from '../libs/event-bus.js'
import { uploadAudio, addVideo } from '../libs/api.js'

export default {
  name: 'Preview',
  components: {
    Player
  },
  data () {
    return {
      dialog: false,
      openMoreMenu: false,
      title: ''
    }
  },
  computed: {
    audioTrack () {
      return this.$store.getters.audioTrack
    },
    canvasObjs () {
      return this.$store.getters.canvasObjs
    },
    duration () {
      return this.$store.getters.duration
    },
    stages () {
      return this.$store.getters.stages
    },
    currentUser () {
      return this.$store.getters.currentUser
    },
    stageBgColor () {
      return this.$store.getters.stageBgColor
    },
    playerSize () {
      return this.$store.getters.playerSize
    }
  },
  watch: {
    currentUser: function (user) {
      if (user) {
        this.dialog = true
      }
    }
  },
  mounted () {
    if (!this.audioTrack.blob.size) {
      this.$toast.error('音频资源已失效')
    }
  },
  methods: {
    openUploadDialog () {
      if (this.currentUser) {
        this.dialog = true
      } else {
        EventBus.$emit('requireLogin')
      }
    },
    publishVideo () {
      if (this.title.length <= 30) {
        EventBus.$emit('load:start', '发布中...')
        uploadAudio(this.audioTrack.blob).then((url) => {
          let size = this.audioTrack.blob.size
          this.stages.forEach(stage => {
            if (stage.bg) {
              size += stage.bg.size
            }
          })
          const coverObj = this.stages.find(s => s.bg)
          addVideo({
            ap: url,
            cd: JSON.stringify({
              stage: this.stages,
              canvas: this.canvasObjs,
              dur: this.duration,
              size: this.playerSize,
              bgcolor: this.stageBgColor,
              resSize: size
            }),
            t: this.title,
            dur: this.duration,
            cover_url: (coverObj ? coverObj.bg.url : '')
          }, (id) => {
            this.title = ''
            this.dialog = false
            EventBus.$emit('load:end')
            this.$router.replace({ path: `/vd/${id}` })
          })
        }).catch(() => {
          this.$toast.error(this.$t('preview.pubFail'))
        })
      }
    }
  }
}
</script>
