<template>
  <div class="player-wrap">
    <div class="canvas-container">
      <canvas-layer
        ref="canvas"
        :isPlayer="true"
        :playerBgColor="bgColor"
        :width="size[0]"
        :wrapHeight="size[1]"
      ></canvas-layer>
      <div class="coverage" @click="handlePlay">
        <template v-if="!currentTs">
          <mu-paper circle :zDepth="1" class="video-ctrl-cover">
            <mu-icon value="play_arrow"/>
          </mu-paper>
          <div class="video-dur">{{hDur}}</div>
        </template>
      </div>
    </div>
    <div class='ctrl-bar' v-show='currentTs'>
      <span class='btn'>
        <i class='mu-icon material-icons' @click='handlePlay'>
          {{currentTs && !isPaused ? 'pause' : 'play_arrow'}}
        </i>
      </span>
      <mu-slider
        v-if="isPaused && canSeek"
        v-model="progress"
        :step="1"
        @change='onChangeProgress'
        track-color="white"
        color="#01579b" />
      <mu-linear-progress
        v-if="!isPaused || !canSeek"
        mode="determinate"
        :size='progressSize'
        color='#fff'
        :value="progress" />
      <span class="time">{{currentTsDisplay}}</span>
    </div>
  </div>
</template>
<script>
import moment from 'moment'
import { Howl } from 'howler'
import CanvasLayer from '../canvas/CanvasLayer'
import { accurateInterval } from '../../libs/utils'
const FRAME_RATE = 1000 / 10

export default {
  name: 'Player',
  components: {
    CanvasLayer
  },
  props: {
    audioUrl: {
      type: String,
      require: true
    },
    canvasObjs: {
      type: Array,
      require: true
    },
    duration: {
      type: Number,
      require: true
    },
    autoPlay: {
      type: Boolean,
      default: false
    },
    stages: {
      type: Array
    },
    bgColor: {
      type: String
    },
    canSeek: {
      type: Boolean,
      default: true
    },
    size: {
      type: Array,
      default () {
        return [900, 506]
      }
    }
  },
  data () {
    return {
      currentTs: 0,
      currentDur: 0,
      progress: 0,
      progressSize: 5,
      isPaused: false,
      framesData: {}
    }
  },
  computed: {
    currentTsDisplay () {
      return moment(this.currentDur).format('mm:ss')
    },
    hDur () {
      return moment(this.duration).format('mm:ss')
    }
  },
  mounted () {
    this.canvasLayer = this.$refs['canvas']
    if (this.autoPlay && this.canvasObjs.length) {
      this.preloadResource(this.startPlay)
    }
  },
  beforeDestroy () {
    if (this.currentTs) {
      this.stopPlay()
    }
    if (this.currentAudio) {
      this.currentAudio.unload()
    }
  },
  methods: {
    startPlay () {
      this.setupPlayerClock()
      this.currentAudio.play()
      this.$emit('played')
      this.canvasLayer.toggleCursor(true)
    },
    pausePlay () {
      if (this.clock) {
        this.clock.cancel()
      }
      this.currentAudio.pause()
      this.isPaused = true
      this.$toast.success('已暂停')
    },
    async resumePlay () {
      // 恢复播放时，如果判断为拖动过进度条，则做相应处理
      if (this.seekToTs && this.seekToTs !== this.currentTs) {
        const fr = this.frameByTs(this.seekToTs)
        this.currentTs = this.seekToTs
        const {
          cd,
          framesData,
          curFid,
          top,
          color
        } = this.parseRealPathByRange(0, this.currentTs)
        this.framesData = framesData
        this.curFid = curFid
        this.frameChange(fr.data.id, top)
        this.canvasLayer.cursorMove({
          curColor: color
        })
        this.canvasLayer.reRenderCanvasData(cd)
        await this.seekAudio(this.currentTs)
        this.seekToTs = 0
      }
      this.setupPlayerClock()
      if (this.isPaused) {
        this.currentAudio.play()
      }
      this.isPaused = false
    },
    stopPlay () {
      if (this.clock) {
        this.clock.cancel()
      }
      if (this.currentAudio) {
        this.currentAudio.stop()
      }
      this.currentTs = 0
      this.isPaused = false
      this.canvasLayer.toggleCursor(false)
      this.canvasLayer.resetLayer()
      this.framesData = {}
      this.curFid = null
      this.canvasLayer.scrollLayerContainer(0)
    },
    handlePlay () {
      if (!this.currentTs) {
        this.preloadResource(this.startPlay)
      } else if (this.isPaused) {
        this.resumePlay()
      } else {
        this.pausePlay()
      }
    },
    doEachTask (cts) {
      return new Promise((resolve, reject) => {
        this.canvasObjs.filter(obj => {
          return (cts - FRAME_RATE <= obj.ts) && (obj.ts < cts)
        }).forEach(this.playEachRecord)
        if (cts % 1000 === 0) {
          this.currentDur = cts
        }
        this.progress = (cts / this.duration) * 100
        resolve()
      })
    },
    setupPlayerClock () {
      this.clock = accurateInterval(FRAME_RATE, () => {
        this.currentTs += FRAME_RATE
        this.doEachTask(this.currentTs)
      })
    },
    playEachRecord (record) {
      switch (record.ac) {
        case 'CSync':
        case 'Cy':
          this.canvasLayer.renderCurve(record.data, 'syncLayer', true)
          const last = record.data.po[record.data.po.length - 1]
          this.canvasLayer.cursorMove({
            curX: last[0],
            curY: last[1],
            curOp: 0.5
          })
          break
        case 'CEnd':
        case 'CE':
          this.canvasLayer.clearCtx('syncLayer')
          this.canvasLayer.renderCurve(record.data)
          break
        case 'E':
          this.canvasLayer.removeCanvasObjs(record.data)
          this.canvasLayer.reRenderCanvasData()
          break
        case 'CM':
          this.canvasLayer.cursorMove({
            curX: record.data[0],
            curY: record.data[1],
            curOp: 1
          })
          break
        case 'F':
          this.preFid = this.curFid
          let top = 0
          if (typeof record.data === 'object') {
            this.curFid = record.data.id
            top = record.data.top
          } else {
            this.curFid = record.data
          }
          if (this.preFid) {
            this.framesData[this.preFid] = this.canvasLayer.canvasObjs
          }
          this.frameChange(this.curFid, top)
          break
        case 'SL':
          this.canvasLayer.scrollLayerContainer(record.data)
          break
        case 'SEnd':
          this.canvasLayer.renderShape(record.data)
          break
        case 'End':
          this.progress = 100
          this.stopPlay()
          break
        case 'CC':
          this.canvasLayer.cursorMove({
            curColor: record.data
          })
          break
        case 'UE':
          this.canvasLayer.batchRenderObjs(record.data)
          for (let obj of record.data) {
            this.canvasLayer.addCanvasObj(obj)
          }
          break
      }
    },
    // 预载入背景素材、音频、笔迹数据分组
    preloadResource (done) {
      // this.groupRecordsByFrameRate()
      this.canvasLayer.preloadStages(this.stages)
      this.loadAudio(done)
    },
    // 预先对records 进行按帧分组，数组下标表示
    groupRecordsByFrameRate () {
      let cur = 0
      let results = []
      while (cur < this.duration) {
        cur += FRAME_RATE
        results.push(this.canvasObjs.filter(obj => {
          return (cur - FRAME_RATE <= obj.ts) && (obj.ts < cur)
        }))
      }
      return results
    },
    loadAudio (done) {
      if (this.currentAudio) {
        done()
        return
      }
      this.currentAudio = new Howl({
        src: [this.audioUrl, this.audioUrl + '.MP3'],
        html5: true,
        format: ['ogg', 'mp3'],
        onload: done,
        onloaderror: () => {
          this.$toast.error('音频加载失败')
        }
      })
    },
    onChangeProgress (v) {
      if (this.canSeek) {
        // ms
        this.seekToTs = Math.round((v * this.duration) / 100)
        this.currentDur = this.seekToTs
      }
    },
    seekAudio (ts) {
      return new Promise((resolve, reject) => {
        if (this.currentAudio) {
          this.currentAudio.once('seek', resolve)
          // sec
          this.currentAudio.seek(ts / 1000)
        } else {
          resolve()
        }
      })
    },
    fitScreenWidth () {
      this.$refs.canvas.setScaleRateBy()
    },
    fitToOriginWidth () {
      this.$refs.canvas.setScaleRateBy({ origin: true })
    },
    // option: to 取得当前ts前后某页
    frameByTs (ts, opts = {}) {
      const fs = this.canvasObjs.filter(re => re.ac === 'F').reverse()
      for (let i in fs) {
        if (fs[i].ts - ts <= 0) {
          if (opts.to) {
            return fs[i - opts.to]
          } else {
            return fs[i]
          }
        }
      }
    },
    frameChange (fid, scrollTop) {
      const curFrame = this.stages.find(stage => stage.id === fid)
      if (curFrame) {
        const newFBg = curFrame.bg
        this.canvasLayer.updateHeight(newFBg ? newFBg.h : this.size[1]).then(() => {
          this.canvasLayer.reRenderCanvasData(this.framesData[curFrame.id] || [])
          if (newFBg) {
            this.canvasLayer.setStageLayer(newFBg, curFrame.id)
          }
          this.canvasLayer.scrollLayerContainer(scrollTop)
        })
      }
    },
    parseRealPathByRange (sts, ets) {
      let preFid, curFid, framesData, top, color
      const VT = ['F', 'CEnd', 'E', 'SEnd', 'CC', 'UE', 'SL']
      let cd = []
      const objs = this.canvasObjs.filter(obj => {
        return (VT.indexOf(obj.ac) > -1) && (sts <= obj.ts) && (obj.ts <= ets)
      })
      if (sts === 0) {
        framesData = {}
        preFid = curFid = null
      }
      for (let obj of objs) {
        switch (obj.ac) {
          case 'CEnd':
          case 'SEnd':
            cd.push(obj.data)
            break
          case 'SL':
            top = obj.data
            break
          case 'CC':
            color = obj.data
            break
          case 'E':
            cd = cd.filter((record) => {
              return obj.data.indexOf(record.id) < 0
            })
            break
          case 'UE':
            if (Array.isArray(obj.data)) {
              cd.concat(obj.data)
            }
            break
          case 'F':
            if (sts === 0) {
              preFid = curFid
              curFid = obj.data
              if (preFid) {
                framesData[preFid] = cd.slice(0)
              }
              cd = framesData[curFid] || []
            } else {
              cd = []
            }
            break
        }
      }
      if (sts === 0) {
        return { cd, framesData, curFid, top, color }
      } else {
        return { cd, top }
      }
    }
  }
}
</script>
<style lang="stylus" scoped src='./player.styl' />
