import Vue from 'vue'
import Router from 'vue-router'
import Editor from '@/views/editor'
import Contact from '@/views/contact'
import MyWork from '@/views/mywork'
import Preview from '@/views/preview'
import Video from '@/views/video'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Editor',
      component: Editor
    },
    {
      path: '/preview',
      name: 'Preview',
      component: Preview
    },
    {
      path: '/mywork',
      name: 'MyWork',
      component: MyWork
    },
    {
      path: '/vd/:id',
      name: 'Video',
      component: Video
    },
    {
      path: '/contact',
      name: 'Contact',
      component: Contact
    }
  ]
})
