import {
  SET_CANVAS_OBJS,
  SET_AUDIO_TRACK,
  SET_STAGE_OBJS,
  SET_STAGE_BG_COLOR,
  SET_PLAYER_SIZE
} from '../mutation-types'

const state = {
  canvasObjs: [],
  audioTrack: {
    url: null,
    blob: null
  },
  stages: [],
  stageBgColor: '#fff',
  playerSize: []
}

const getters = {
  canvasObjs: state => state.canvasObjs,
  duration: state => {
    const endAc = state.canvasObjs.find(obj => obj.ac === 'End')
    return endAc ? endAc.ts : 0
  },
  audioTrack: state => state.audioTrack,
  stages: state => state.stages,
  stageBgColor: state => state.stageBgColor,
  playerSize: state => state.playerSize
}

const mutations = {
  // 发布上传后清空草稿
  [SET_CANVAS_OBJS] (state, objs) {
    state.canvasObjs = objs
  },
  [SET_AUDIO_TRACK] (state, track) {
    state.audioTrack = track
  },
  [SET_STAGE_OBJS] (state, frames) {
    state.stages = frames
  },
  [SET_STAGE_BG_COLOR] (state, color) {
    state.stageBgColor = color
  },
  [SET_PLAYER_SIZE] (state, size) {
    state.playerSize = size
  }
}

export default {
  state,
  getters,
  mutations
}
