<template>
  <section>
    <mu-appbar color="primary" :title="$t('mywork.title')" :z-depth='0'>
      <mu-button icon slot="left" to="/">
        <mu-icon value="arrow_back" />
      </mu-button>
      <mu-list slot="right" v-if="currentUser">
        <mu-list-item>
          <mu-list-item-title class="user-account">
            {{currentUser._email}}
          </mu-list-item-title>
        </mu-list-item>
      </mu-list>
      <mu-menu
        placement='bottom-end'
        slot="right"
        :open.sync="openMoreMenu"
        v-if="currentUser">
        <mu-button flat>
          <mu-icon value="more_vert" />
        </mu-button>
        <mu-list slot="content">
          <mu-list-item button @click='handleLogout'>
            <mu-list-item-title>{{$t('mywork.changeAccount')}}</mu-list-item-title>
          </mu-list-item>
        </mu-list>
      </mu-menu>
      <mu-button
        flat
        v-if="!currentUser"
        primary
        slot="right"
        @click='requireLogin'>
        {{$t('mywork.login')}}
      </mu-button>
    </mu-appbar>
    <div class="top-action">
      <div v-if="allCount" class='list-item-count'>{{$t('mywork.created')}} {{allCount}}</div>
    </div>
    <div class="worklist">
      <mu-load-more :loading="loading" @load="loadMore">
        <mu-list v-if="currentUser" textline="two-line">
          <template v-if="workList">
            <div class='empty-tip' v-if="workList.length === 0">暂无任何作品</div>
            <mu-list-item
              button
              ripple
              avatar
              v-for="(item, i) in workList"
              :key="i"
              :to="'/vd/' + item.id"
            >
              <mu-list-item-action>
                <mu-icon value="videocam" color='#3f51b5'></mu-icon>
              </mu-list-item-action>
              <mu-list-item-content>
                <mu-list-item-title>{{item.t}}</mu-list-item-title>
                <mu-list-item-sub-title><small>创建于</small> {{item.c_at}}</mu-list-item-sub-title>
              </mu-list-item-content>
              <mu-list-item-action>
                <mu-list-item-after-text class="vd-dur">{{item.duration}}</mu-list-item-after-text>
              </mu-list-item-action>
            </mu-list-item>
          </template>
        </mu-list>
      </mu-load-more>
    </div>
  </section>
</template>
<script>
import moment from 'moment'
import { getVideos } from '../libs/api'
import { EventBus } from '../libs/event-bus.js'
moment.locale('zh-CN')

export default {
  name: 'MyWork',
  data () {
    return {
      allCount: 0,
      workList: null,
      loading: false,
      openMoreMenu: false,
      uploadAttrs: {
        accept: 'application/zip'
      },
      uploadOptions: {
        testChunks: false,
        singleFile: true
      }
    }
  },
  computed: {
    currentUser () {
      return this.$store.getters.currentUser
    }
  },
  watch: {
    currentUser: function (user) {
      this.checkLogin(user)
    }
  },
  mounted () {
    this.checkLogin(this.currentUser)
  },
  methods: {
    checkLogin (user) {
      if (user) {
        EventBus.$emit('load:start')
        this.loadVideos({
          uid: user.id
        }, () => {
          EventBus.$emit('load:end')
        })
      } else {
        this.requireLogin()
      }
    },
    requireLogin () {
      EventBus.$emit('requireLogin')
    },

    loadVideos (opts = {}, done) {
      this.query = opts
      getVideos(opts, (list, hasNext, total) => {
        this.hasNext = hasNext
        let newList = list
        if (opts.offset) {
          newList = this.list.concat(list)
        }
        this.workList = newList
        this.allCount = total
        if (done) {
          done()
        }
      })
    },
    loadMore () {
      if (this.hasNext) {
        const params = Object.assign(this.query, {
          offset: this.workList.length
        })
        this.loadVideos(params)
      }
    },
    handleLogout () {
      this.$store.commit('SET_CURRENT_USER', null)
    }
  }
}
</script>
<style lang="stylus" scoped src="./mywork.styl" />
