<template>
  <section class="e-container">
    <mu-appbar color="primary" class='record-ctrl' :z-depth='0'>
      <mu-button icon slot="left" class="logo">
        <mu-icon value=":icon-17up" />
      </mu-button>
      <mu-button
        class="frame-nav-disp"
        flat
        primary
        @click="showFrameListPop = true"
        slot="left">{{frameNavDisplay}}</mu-button>
      <mu-button
        flat
        primary
        @click='startRecord'
        class="record-btn"
        :class="{recording: currentTs}"
        slot="right">
        <mu-icon left value='fiber_manual_record' />
        {{currentTs ? currentTsDisplay : $t('editor.recordStart') }}
      </mu-button>
      <mu-button
        flat
        v-show='currentTs'
        @click='pauseRecord'
        slot="right"
      >
        <mu-icon left :value="recordPaused ? 'play_arrow' : 'pause'" />
        {{recordPaused ? $t('editor.resume') : $t('editor.pause')}}
      </mu-button>
      <mu-menu slot="right" :open.sync="openMoreMenu" placement='bottom-end'>
        <mu-button flat>
          <mu-icon value="more_vert" />
        </mu-button>
        <mu-list slot="content">
          <mu-list-item button to="/mywork">
            <mu-list-item-title>{{$t('editor.mywork')}}</mu-list-item-title>
          </mu-list-item>
          <mu-list-item button @click='showPreference = true'>
            <mu-list-item-title>{{$t('editor.preference')}}</mu-list-item-title>
          </mu-list-item>
          <mu-list-item button to="/contact">
            <mu-list-item-title>{{$t('editor.feedback')}}</mu-list-item-title>
          </mu-list-item>
        </mu-list>
      </mu-menu>
    </mu-appbar>
    <mu-dialog
      fullscreen
      transition='none'
      dialog-class="frame-list-pop"
      :open.sync="showFrameListPop">
      <mu-appbar color="primary" :title="$t('editor.viewFrameList')" :z-depth='0'>
        <mu-button icon @click="showFrameListPop = false" slot="left">
          <mu-icon value="close" />
        </mu-button>
        <mu-button
          flat
          slot="right"
          @click="removeCurFrame">
          <mu-icon left value="delete_forever" />
          {{$t('editor.deleteCurFrame')}}
        </mu-button>
      </mu-appbar>
      <div class="list-container" v-lazy-container="{ selector: 'img' }">
        <div
          class="frame-item"
          :class="{cur: (currentFrame === i + 1)}"
          v-for='(frame, i) in frameList'
          :data-num='i + 1'
          @click='() => handleNavToFrame(i)'
          :key='frame.id'>
          <img v-if="frame.bg" :data-src="CDN_PF + frame.bg.url + '!/fw/200'" :alt="i + 1">
        </div>
      </div>
    </mu-dialog>
    <div class="frame-ctrl nav-prev" @click='() => handleNavFrame(-1)'>
      <mu-icon value='chevron_left' />
    </div>
    <div class="frame-ctrl nav-next" @click='() => handleNavFrame(1)'>
      <mu-icon :value="currentFrame < frameList.length ? 'chevron_right' : 'plus_one'" />
    </div>
    <div
      ref="canvasWrap"
      class="canvas-container"
      :style='maxCanvasH'
      @scroll='handleScroll'>
      <canvas-layer
        ref="canvas"
        @capture='handleCapture'
        :width="viewSize[0]"
        :wrapHeight="viewSize[1]"
      ></canvas-layer>
    </div>
    <div class="divider">{{$t('editor.canvasHTip')}}{{viewSize[1]}}px</div>
    <control-bar
      @convertPdf='handleConvertPdf'
      @compressImage='handleCompressImage'
      @undo='handleUndoEarse'></control-bar>
    <mu-dialog
      width='400'
      :title="$t('editor.pausedRecordTip')"
      :open.sync="recordPaused"
      :overlay-close='false'>
      <mu-button flat slot="actions" color="primary" @click="pauseRecord">
        {{$t('editor.recordResume')}}
      </mu-button>
    </mu-dialog>
    <mu-dialog
      :title="$t('editor.preference')"
      width='460'
      :open.sync="showPreference">
      <mu-list>
        <mu-sub-header>{{$t('editor.canvasPref')}}</mu-sub-header>
        <mu-list-item button :ripple="false" @click="blackboard = !blackboard">
          <mu-list-item-title>
            {{$t('editor.blackboardMode')}} <small class="second">{{$t('editor.blackboardModeSub')}}</small>
          </mu-list-item-title>
          <mu-list-item-action>
            <mu-switch v-model="blackboard" readonly/>
          </mu-list-item-action>
        </mu-list-item>
        <mu-sub-header>{{$t('editor.pencilPref')}}</mu-sub-header>
        <mu-list-item :ripple="false">
          <mu-list-item-title>
            {{$t('editor.pencilT')}} {{tolerance}} <small class="second">{{$t('editor.pencilTSub')}}</small>
          </mu-list-item-title>
          <mu-list-item-action>
            <mu-slider
              class="tolerance-slider"
              v-model="tolerance"
              :max="8"
              :step="1"
              :min="1"
            />
          </mu-list-item-action>
        </mu-list-item>
        <mu-sub-header>{{$t('editor.langPref')}}</mu-sub-header>
        <mu-list-item>
          <mu-select v-model="lang" full-width>
            <mu-option label="中文" value="zh" />
            <mu-option label="English" value="en" />
          </mu-select>
        </mu-list-item>
      </mu-list>
    </mu-dialog>
  </section>
</template>
<script>
import _ from 'lodash'
import CanvasLayer from '@/components/canvas/CanvasLayer'
import ControlBar from '@/components/canvas/ControlBar'
import Recorder from 'opus-recorder'
import moment from 'moment'
import { uploadImg } from '../libs/api'
import { accurateInterval } from '../libs/utils'
import { CDN_PF } from '../libs/constant'
import { EventBus } from '../libs/event-bus.js'
const FRAME_RATE = 100

export default {
  name: 'Editor',
  data () {
    return {
      showFrameListPop: false,
      openMoreMenu: false,
      currentTs: 0,
      currentDur: 0,
      recordPaused: false,
      framesData: {},
      CDN_PF,
      showPreference: false
    }
  },
  components: {
    CanvasLayer,
    ControlBar
  },
  mounted () {
    if (!Recorder.isRecordingSupported()) {
      this.$toast.error(this.$t('editor.recordNotSupport'))
    }
    const curFrameObj = this.frameList[this.currentFrame - 1]
    if (curFrameObj.bg) {
      this.$refs.canvas.updateHeight(curFrameObj.bg.h).then(() => {
        this.$refs.canvas.setStageLayer(curFrameObj.bg, curFrameObj.id)
      })
    }
    this.currentAc = null
    // /js proxy to CDN
    this.auRecorder = new Recorder({
      encoderPath: '/encoderWorker.min.js'
    })
    this.auRecorder.onstart = () => {
      this.records = []
      this.currentTs = 1
      this.$toast.success(this.$t('editor.recordStart'))
      const scrollTop = this.$refs.canvasWrap.scrollTop
      // 'F' type recorder structor
      // can be object {id, top} or id string
      this.addAcRecord('F', {
        id: this.currentFid,
        top: scrollTop
      })
      this.addAcRecord('CC', this.currentColor)
      this.setupRecordClock()
    }
    this.auRecorder.onpause = () => {
      this.recordPaused = true
      this.clock.cancel()
      this.$toast.success(this.$t('editor.paused'))
    }
    this.auRecorder.onresume = () => {
      this.recordPaused = false
      this.currentAc = null
      this.setupRecordClock()
      this.$toast.success(this.$t('editor.resumed'))
    }
    this.auRecorder.onstop = () => {
      this.addAcRecord('End')
      this.clock.cancel()
      this.currentTs = 0
      this.$store.commit('SET_CANVAS_OBJS', this.records)
      // 记录所有录制过程中用到的frame为stage
      const fs = _.uniq(this.records.filter(r => r.ac === 'F').map(r => {
        return typeof r.data === 'object' ? r.data.id : r.data
      })).map(fid => {
        return this.frameList.find(f => f.id === fid)
      })
      this.$store.commit('SET_STAGE_OBJS', fs)
      this.$store.commit('SET_STAGE_BG_COLOR', this.currentBgColor)
      this.$store.commit('SET_PLAYER_SIZE', this.viewSize)
      this.$router.push({ path: 'preview' })
    }
    this.auRecorder.ondataavailable = (data) => {
      const blobAudio = new Blob([data], { type: 'audio/ogg' })
      const url = URL.createObjectURL(blobAudio)
      this.$store.commit('SET_AUDIO_TRACK', {
        url,
        blob: blobAudio
      })
    }
  },
  computed: {
    currentTsDisplay () {
      return moment(this.currentDur).format('mm:ss')
    },
    currentColor () {
      return this.$store.getters.currentColor
    },
    currentBgColor () {
      return this.$store.getters.currentBgColor
    },
    currentFrame () {
      return this.$store.getters.currentFrame
    },
    frameList () {
      return this.$store.getters.frameList
    },
    currentFid () {
      return this.$store.getters.currentFid
    },
    frameNavDisplay () {
      return this.$t('editor.frameNo', {
        cur: this.currentFrame,
        len: this.frameList.length
      })
    },
    viewSize () {
      return this.$store.getters.viewSize
    },
    maxCanvasH () {
      return 'max-height:' + this.viewSize[1] + 'px;'
    },
    blackboard: {
      get () {
        return this.currentBgColor !== '#fff'
      },
      set (tof) {
        this.$store.commit('CHANGE_CANVAS_BG_COLOR', tof ? '#2C3E50' : '#fff')
      }
    },
    tolerance: {
      get () {
        return this.$store.getters.preferences.tolerance
      },
      set (v) {
        this.$store.commit('SET_PREFERENCE', { tolerance: v })
      }
    },
    lang: {
      get () {
        return this.$store.getters.preferences.lang
      },
      set (v) {
        this.$store.commit('SET_PREFERENCE', { lang: v })
      }
    }
  },
  watch: {
    currentColor: function (newV, oldV) {
      this.addAcRecord('CC', newV)
    },
    currentFrame: function (newV, oldV) {
      const oldFrame = this.frameList[oldV - 1]
      if (oldFrame) {
        this.framesData[oldFrame.id] = this.$refs.canvas.canvasObjs
      }
      this.loadFrameContent(newV)
      this.skipCapture = true
      this.$refs.canvasWrap.scrollTop = 0
    },
    lang: function (newV) {
      this._i18n.locale = newV
    }
  },
  methods: {
    // 控制录制开始/结束
    startRecord () {
      if (this.currentTs) {
        this.auRecorder.stop()
      } else {
        this.auRecorder.start()
      }
    },
    // 控制录制暂停/继续
    pauseRecord () {
      if (this.recordPaused) {
        this.auRecorder.resume()
      } else {
        this.auRecorder.pause()
      }
    },
    setupRecordClock () {
      this.clock = accurateInterval(FRAME_RATE, () => {
        this.currentTs += FRAME_RATE
        if (this.currentTs % 1000 === 1) {
          this.currentDur = this.currentTs
        }
        if (this.currentAc) {
          this.records.push(this.currentAc)
          this.currentAc = null
        }
      })
    },
    handleCapture (data) {
      if (this.currentTs) {
        if (this.currentAc) {
          if (this.currentAc.ac !== data.ac) {
            if (this.currentAc.ac === 'CEnd') {
              return
            }
          }
        }
        this.currentAc = Object.assign({}, data, {
          ts: this.currentTs
        })
      }
    },
    addAcRecord (ac, data = {}) {
      if (this.currentTs) {
        this.records.push({ ac, ts: this.currentTs, data })
      }
    },
    handleUndoEarse () {
      this.$refs.canvas.redrawEarseHistory()
    },
    handleNavFrame (val) {
      const frameNo = this.currentFrame + val
      this.$store.commit('CHANGE_CURRENT_FRAME', frameNo)
    },
    handleNavToFrame (i) {
      this.$store.commit('CHANGE_CURRENT_FRAME', i + 1)
      this.showFrameListPop = false
    },
    removeCurFrame () {
      this.$confirm(('确定要删除当前第 ' + this.currentFrame + ' 页吗？'), '确认操作', {
        type: 'warning',
        beforeClose: (result, instance, done) => {
          if (result) {
            this.$store.commit('REMOVE_FRAME_BY_NUM', this.currentFrame)
            if (this.currentFrame > this.frameList.length) {
              // 如果当前只有一页，删除后自动增加一页空白页
              const toFrame = this.frameList.length === 0 ? 1 : this.currentFrame - 1
              this.$store.commit('CHANGE_CURRENT_FRAME', toFrame)
            } else {
              this.loadFrameContent(this.currentFrame)
            }
          }
          done()
        }
      })
    },
    loadFrameContent (num) {
      const newFid = this.frameList[num - 1].id
      const newFBg = this.frameList[num - 1].bg
      this.$refs.canvas.updateHeight(newFBg ? newFBg.h : this.viewSize[1]).then(() => {
        this.$refs.canvas.reRenderCanvasData(this.framesData[newFid] || [])
        if (newFBg) {
          this.$refs.canvas.setStageLayer(newFBg, newFid)
        }
      })
      this.addAcRecord('F', this.currentFid)
    },
    handleScroll (e) {
      if (this.skipCapture) {
        this.skipCapture = false
        return
      }
      this.handleCapture({
        ac: 'SL',
        data: e.target.scrollTop
      })
    },
    async handleConvertPdf (obj) {
      let i = 1
      let imgList = []
      while (i <= obj.missionNum) {
        EventBus.$emit(
          'load:start',
          '正在导入第 ' + i + ' 页/共 ' + obj.missionNum + ' 页'
        )
        await new Promise((resolve, reject) => {
          this.$refs.canvas.convertPDF2PNG(obj.pdf, i)
            .then((b64) => {
              uploadImg(b64, true).then((img) => {
                imgList.push(img)
                resolve()
              })
            })
        })
        i++
      }
      this.$store.commit('INSERT_FRAME_BY_IMAGES', imgList)
      EventBus.$emit('load:end')
    },
    handleCompressImage (url) {
      this.$refs.canvas.putImageAsBg(url).then(b64 => {
        uploadImg(b64, true).then((img) => {
          this.$store.commit('INSERT_FRAME_BY_IMAGE', img)
          EventBus.$emit('load:end')
        })
      })
    }
  }
}
</script>
<style lang="stylus" src='./editor.styl' scoped />
