<template>
  <section>
    <mu-appbar color='primary' :z-depth='0' class="video-header" :title='info.t'>
      <mu-button icon slot="left">
        <mu-icon value=":icon-17up" />
      </mu-button>
      <mu-list slot="right">
        <mu-list-item class="author-card">
          <mu-list-item-title class="author-name">{{info.created_by.nickname}}</mu-list-item-title>
        </mu-list-item>
      </mu-list>
      <mu-menu
        v-if="info.c_at"
        slot="right"
        :open.sync="openMoreMenu"
        placement='bottom-end'>
        <mu-button flat>
          <mu-icon value="more_vert" />
        </mu-button>
        <mu-list slot="content">
          <mu-list-item button @click='requestFullscreen'>
            <mu-list-item-title>全屏播放</mu-list-item-title>
          </mu-list-item>
          <mu-list-item button v-if="info.xcxcode" @click='showQrcode = true'>
            <mu-list-item-title>扫码观看</mu-list-item-title>
          </mu-list-item>
          <mu-list-item button to='/'>
            <mu-list-item-title>去创作</mu-list-item-title>
          </mu-list-item>
          <mu-list-item button v-if='isAuthor' @click='removeVideo'>
            <mu-list-item-title class="remove-menu">删除</mu-list-item-title>
          </mu-list-item>
        </mu-list>
      </mu-menu>
    </mu-appbar>
    <div class="preview-container">
      <player
        ref="player"
        v-if='info.cd'
        :audioUrl='info.ap'
        :canvasObjs='info.cd.canvas'
        :stages='info.cd.stage'
        @played='plusPlayCount'
        :duration='info.dur'
        :size='info.cd.size'
        :bgColor='info.cd.bgcolor'
      />
    </div>
    <mu-dialog
      dialog-class="qrcode-dialog"
      :open.sync="showQrcode">
      <img :src="info.xcxcode" alt="">
      <div>请使用微信扫一扫观看</div>
    </mu-dialog>
  </section>
</template>
<script>
import Player from '@/components/base/player'
// import { CDN_PF } from '../libs/constant'
import { EventBus } from '../libs/event-bus.js'
import { getVideo, removeVideo, plusVideoCount } from '../libs/api.js'

export default {
  name: 'Video',
  components: {
    Player
  },
  data () {
    return {
      info: {
        t: '',
        created_by: {
          nickname: ''
        }
      },
      showQrcode: false,
      openMoreMenu: false
    }
  },
  computed: {
    currentUser () {
      return this.$store.getters.currentUser
    },
    isAuthor () {
      return this.currentUser && this.currentUser.id === this.info.created_by.id
    }
  },
  watch: {
    // currentUser: function (user) {
    // }
  },
  mounted () {
    this.getVideoBy(this.$route.params.id)
    window.addEventListener('orientationchange', () => {
      this.$refs.player.fitScreenWidth()
      if (window.screen.orientation && window.screen.orientation.type === 'landscape-primary') {
        this.$confirm('建议开启全屏模式, 获取最佳观看体验', '提示', {
          type: 'info',
          beforeClose: (result, instance, done) => {
            if (result) {
              this.requestFullscreen()
            }
            done()
          }
        })
      }
    })
    document.addEventListener('webkitfullscreenchange', (event) => {
      const isFullScreen = document.mozFullScreen || document.webkitIsFullScreen
      if (!isFullScreen) {
        this.$refs.player.fitToOriginWidth()
      }
    })
  },
  methods: {
    getVideoBy (id) {
      EventBus.$emit('load:start')
      getVideo(id, item => {
        this.info = item
        this.$nextTick(() => {
          if (window.screen.width < 900) {
            this.$refs.player.fitScreenWidth()
          }
        })
        EventBus.$emit('load:end')
      })
    },
    plusPlayCount () {
      plusVideoCount(this.$route.params.id)
    },
    requestFullscreen (e) {
      this.openMoreMenu = false
      const el = this.$refs.player.$el
      if (e) {
        this.$refs.player.fitScreenWidth()
      }
      if (el.requestFullscreen) {
        el.requestFullscreen()
      } else if (el.webkitRequestFullscreen) {
        el.webkitRequestFullscreen()
      } else if (el.mozRequestFullScreen) {
        el.mozRequestFullScreen()
      } else if (el.msRequestFullscreen) {
        el.msRequestFullscreen()
      }
    },
    removeVideo () {
      this.openMoreMenu = false
      this.$confirm(('确定要删除 ' + this.info.t + ' 吗？无法恢复'), '确认操作', {
        type: 'warning',
        beforeClose: (result, instance, done) => {
          if (result) {
            EventBus.$emit('load:start', '正在删除...')
            removeVideo(this.info.id, res => {
              EventBus.$emit('load:end')
              this.$router.replace({ path: '/mywork' })
            })
          }
          done()
        }
      })
    }
  }
}
</script>
<style lang="stylus" src='./video.styl' />
